body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 10px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button {
    background: #FFCC00;
    border: none;
    cursor: pointer;
    color: #FFF;
    font-size: 18px;
    border-radius: 4px;
    padding: 6px 15px;
    min-width: 30px;
}
button.red {
  background: #f30000;
}
button.blue {
  background: #3baaff;
}
button.m-lf {
  margin-left: 5px;
}
select {
    font-size: 16px;
    border-radius: 4px;
    padding: 4px 13px;
    min-width: 100px;
}
button:hover {
    color: #FFF;
}
a {
    text-decoration: none;
    color: #000;
}
a:visited {
    text-decoration: none;
    color: none;
}
button:active, button:focus, select, textarea, input:focus {
  outline: none;
}
button::-moz-focus-inner {
  border: 0;
}
table {
    width: 100%;
    max-width: 1080px;
}
th {
    text-align: left;
}
tbody tr:hover {
    background: #000;
    color: #FFF;
}
tbody td {
    padding: 5px;
}